import React, { Component } from "react";
import validator from "validator";
import Header from "../NotAuthHeader/Header";
import url from "../../strings/url";
import {
  Container,
  Button,
  ThemeProvider,
  createMuiTheme,
  IconButton,
  Backdrop,
  CircularProgress,
  Snackbar,
} from "@material-ui/core";
import { ArrowForward, ArrowBack } from "@material-ui/icons";
import Stage1 from "./stage1/stage1";
import { orange, red, blue } from "@material-ui/core/colors";
import { Divider } from "semantic-ui-react";
import Stage2 from "./stage2/stage2";
import Stage3 from "./stage3/stage3";
import moment from "moment";

const buttonTheme = createMuiTheme({
  palette: {
    primary: orange,
    secondary: {
      main: blue[600],
    },
    third: red,
  },
});

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFetchingEmail: false,
      email: "",
      isEmailRight: "unknown",
      isUserNameRight: "unknown",
      firstName: "",
      LastName: "",
      password: "",
      repass: "",
      gender: "",
      username: "",
      dob: moment(new Date("12/30/2005")),
      yearofjoining: "2017",
      stage: 1,
      college: "",
      phone: "",
      Branch: "",
      course: "",
      rollno: "",
      isLoading: false,
      isError: false,
      isMobileLoading: false,
      phoneValid: "unknown",
      isUserNameLoading: false,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleDateChange = this.handleDateChange.bind(this);
    this.handleYearChange = this.handleYearChange.bind(this);
    this.onBlur = this.onBlur.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onBlurUser = this.onBlurUser.bind(this);
    this.onBlurPhone = this.onBlurPhone.bind(this);
  }
  handleSubmit() {
    this.setState({ isLoading: true });
    const headers = new Headers();
    console.log("hello");
    headers.append("content-type", "application/json");
    const {
      email,
      firstName,
      LastName,
      password,
      Branch,
      course,
      phone,
      rollno,
      college,
      yearofjoining,
      dob,
      username,
      gender,
    } = this.state;
    const body = JSON.stringify({
      email,
      firstName,
      LastName,
      password,
      Branch,
      course,
      phone,
      rollno,
      college,
      yearofjoining,
      dob,
      username,
      gender,
    });
    const request = new Request(`${url}/user/signup`, {
      method: "POST",
      body,
      headers,
    });
    fetch(request)
      .then(async (res) => {
        if (res.status === 201) {
          const { token } = await res.json();
          localStorage.setItem("AnonymityToken#123", token);
          this.props.history.push("/anonymity/home");
        } else if (res.status === 400) {
          this.setState({ isError: true, isLoading: false });
        }
      })
      .catch((e) => {
        this.setState({ isError: true, isLoading: false });
      });
  }
  onBlur(e) {
    if (validator.default.isEmail(this.state.email)) {
      const headers = new Headers();
      headers.append("content-type", "application/json");
      const request = new Request(
        `${url}/user/verifyEmail/${this.state.email}`,
        {
          method: "GET",
          headers,
        }
      );
      this.setState({ isFetchingEmail: true });
      fetch(request)
        .then(async (res) => {
          try {
            const response = await res.json();
            this.setState({
              isFetchingEmail: false,
              isEmailRight: response.isEmailValid,
            });
          } catch (e) {
            this.setState({ isFetchingEmail: false });
          }
        })
        .catch((e) => {
          this.setState({ isFetchingEmail: false });
        });
    } else if (e.target.value !== "") {
      this.setState({ isEmailRight: false });
    }
  }
  onBlurUser(e) {
    if ((e.target.name = "username")) {
      if (e.target.value !== "") {
        const headers = new Headers();
        headers.append("content-type", "application/json");
        const request = new Request(`${url}/user/username/${e.target.value}`, {
          method: "GET",
          headers,
        });
        this.setState({ isUserNameLoading: true });
        fetch(request)
          .then(async (res) => {
            const response = await res.json();
            this.setState({
              isUserNameRight: response.isValid,
              isUserNameLoading: false,
            });
          })
          .catch((e) => {
            this.setState({
              isUserNameRight: "unknown",
              isUserNameLoading: false,
            });
          });
      }
    }
  }
  onBlurPhone(e) {
    if (e.target.value.length === 10) {
      console.log("hello");
      const headers = new Headers();
      headers.append("content-type", "application/json");
      const request = new Request(`${url}/user/phone/${e.target.value}`, {
        method: "GET",
        headers,
      });
      this.setState({ isMobileLoading: true });
      fetch(request)
        .then(async (response) => {
          try {
            console.log(response.status);
            const res = await response.json();
            if (response.status === 200) {
              return this.setState({
                isMobileLoading: false,
                phoneValid: true,
              });
            }
            this.setState({ isMobileLoading: false, phoneValid: false });
          } catch (e) {
            console.log(e.message);
          }
        })
        .catch((e) => {
          console.log(e.message);
          this.setState({ isMobileLoading: false, phoneValid: "unknown" });
        });
    }
  }
  handleChange(e) {
    this.setState({ isUserNameLoading: false });
    if (e.target.name == "username") {
      this.setState({ isUserNameRight: "unknown" });
      if (!e.target.value.includes(" ")) {
        return this.setState({ [e.target.name]: e.target.value });
      }
      return;
    }
    if (e.target.name == "course") {
      this.setState({ Branch: "" });
    } else if (e.target.name === "phone" || e.target.name === "rollno") {
      if (e.target.name === "phone") {
        this.setState({ phoneValid: "unknown" });
      }
      const isValid =
        validator.isNumeric(e.target.value) || e.target.value === "";
      if (!isValid) {
        return;
      }
      return this.setState({ [e.target.name]: e.target.value });
    } else if (e.target.name == "email") {
      this.setState({ isEmailRight: "unknown" });
    }

    this.setState({ [e.target.name]: e.target.value });
  }
  handleDateChange(date) {
    this.setState({ dob: date });
  }
  handleYearChange(year) {
    this.setState({ yearofjoining: year.format("YYYY") });
  }

  render() {
    const {
      isEmailRight,
      password,
      repass,
      firstName,
      LastName,
      gender,
    } = this.state;
    return (
      <div>
        <Backdrop open={this.state.isLoading} style={{ zIndex: 100 }}>
          <CircularProgress
            style={{ color: "white", width: 100, height: 100 }}
          ></CircularProgress>
        </Backdrop>
        <Snackbar
          autoHideDuration={3000}
          onClose={() => {
            this.setState({ isError: false });
          }}
          open={this.state.isError}
          message="Error !! Signing Up"
        ></Snackbar>
        <Container maxWidth="sm">
          <Header heading="Anonymity SignUp"></Header>
          {this.state.stage === 1 ? (
            <Stage1
              onBlur={this.onBlur}
              handleChange={this.handleChange}
              {...this.state}
            ></Stage1>
          ) : (
            ""
          )}
          {this.state.stage === 2 ? (
            <Stage2
              onChange={this.handleChange}
              handleDateChange={this.handleDateChange}
              handleYearChange={this.handleYearChange}
              {...this.state}
              onBlurUser={this.onBlurUser}
            ></Stage2>
          ) : (
            ""
          )}
          {this.state.stage === 3 ? (
            <Stage3
              onChange={this.handleChange}
              onBlurPhone={this.onBlurPhone}
              {...this.state}
            ></Stage3>
          ) : (
            ""
          )}
          <ThemeProvider theme={buttonTheme}>
            {this.state.stage == 3 ? (
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  marginTop: 30,
                }}
              >
                <Button
                  disabled={
                    !(
                      this.state.phone.length == 10 &&
                      this.state.rollno &&
                      this.state.course &&
                      this.state.Branch
                    )
                  }
                  onClick={this.handleSubmit}
                  variant="outlined"
                  color="secondary"
                >
                  Sign Up
                </Button>
              </div>
            ) : (
              ""
            )}
            {this.state.stage === 1 ? (
              <div style={{ display: "flex", justifyContent: "center" }}>
                <IconButton
                  color="primary"
                  onClick={() => {
                    this.setState((state) => ({ stage: state.stage + 1 }));
                  }}
                  disabled={
                    !(
                      isEmailRight === true &&
                      repass === password &&
                      password !== "" &&
                      gender &&
                      firstName &&
                      LastName
                    )
                  }
                  style={{ marginTop: 10 }}
                >
                  <ArrowForward></ArrowForward>
                </IconButton>
              </div>
            ) : (
              ""
            )}
            {this.state.stage === 2 ? (
              <div style={{ display: "flex", justifyContent: "center" }}>
                <IconButton
                  color="primary"
                  disabled={
                    !(
                      this.state.isUserNameRight === true &&
                      parseInt(this.state.dob.format("YYYY")) <=
                        parseInt(new Date().getFullYear()) - 17 &&
                      parseInt(this.state.yearofjoining) >
                        parseInt(new Date().getFullYear()) - 4 &&
                      this.state.college
                    )
                  }
                  onClick={() => {
                    this.setState((state) => ({ stage: state.stage + 1 }));
                  }}
                  style={{ marginTop: 10 }}
                >
                  <ArrowForward></ArrowForward>
                </IconButton>
              </div>
            ) : (
              ""
            )}

            <Divider horizontal style={{ color: "grey" }}>
              Or
            </Divider>
            {this.state.stage == 1 ? (
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Button
                  style={{
                    marginTop: 10,
                    textTransform: "lowercase",
                    fontWeight: 400,
                  }}
                  size="large"
                  onClick={() => {
                    this.props.history.push("/login");
                  }}
                >
                  Already Have an account ?
                </Button>
              </div>
            ) : (
              ""
            )}
            {this.state.stage > 1 ? (
              <div style={{ display: "flex", justifyContent: "center" }}>
                <IconButton
                  color="secondary"
                  style={{ marginTop: 10 }}
                  onClick={() => {
                    this.setState((state) => ({ stage: state.stage - 1 }));
                  }}
                >
                  <ArrowBack></ArrowBack>
                </IconButton>
              </div>
            ) : (
              ""
            )}
          </ThemeProvider>
        </Container>
      </div>
    );
  }
}
