import React, { Fragment } from "react";
import MomentUtil from "@date-io/moment";
import {
  TextField,
  createMuiTheme,
  ThemeProvider,
  MenuItem,
  InputAdornment,
  CircularProgress
} from "@material-ui/core";
import { DatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";

import { green, orange } from "@material-ui/core/colors";
import { Check } from "@material-ui/icons";
const theme1 = createMuiTheme({
  palette: {
    primary: orange,
    secondary: green
  }
});
export default props => {
  return (
    <div>
      <ThemeProvider theme={theme1}>
        <TextField
          fullWidth
          label="Username"
          name="username"
          error={props.isUserNameRight == false}
          onChange={props.onChange}
          onBlur={props.onBlurUser}
          helperText={
            props.isUserNameRight == false ? "Username is already Taken" : ""
          }
          FormHelperTextProps={{ className: "comingFromLeft" }}
          InputProps={{
            endAdornment: (
              <InputAdornment>
                {props.isUserNameLoading ? (
                  <CircularProgress
                    style={{ width: 20, height: 20 }}
                  ></CircularProgress>
                ) : (
                  ""
                )}
                {props.isUserNameRight === true ? <Check></Check> : ""}
              </InputAdornment>
            )
          }}
          value={props.username}
          className="comingFromRight"
          placeholder="Anything you Like But Make Sure It's Unique"
        ></TextField>
        <MuiPickersUtilsProvider utils={MomentUtil}>
          <DatePicker
            label="Date Of Birth"
            fullWidth
            error={
              parseInt(props.dob.format("YYYY")) >
              parseInt(new Date().getFullYear()) - 17
            }
            helperText={
              parseInt(props.dob.format("YYYY")) >
              parseInt(new Date().getFullYear()) - 17
                ? `Must Be Less Than ${parseInt(new Date().getFullYear()) - 17}`
                : ""
            }
            FormHelperTextProps={{ className: "comingFromLeft" }}
            className="comingFromLeft"
            style={{ marginTop: 20 }}
            value={props.dob}
            onChange={props.handleDateChange}
            format="MM/DD/YYYY"
          ></DatePicker>
          <TextField
            label="College"
            placeholder="You Study In"
            select
            fullWidth
            style={{ marginTop: 10 }}
            className="comingFromLeft"
            onChange={props.onChange}
            value={props.college}
            name="college"
          >
            <MenuItem
              key="sdiet"
              value="Satyug Darshan Institute Of Technology And Management"
            >
              Satyug Darshan Institute Of Technology And Management
            </MenuItem>
          </TextField>
          <DatePicker
            label="Year Of Joining #College"
            fullWidth
            helperText={`Must Be Greater Than or Equal To ${parseInt(
              new Date().getFullYear()
            ) - 4}`}
            views={["year"]}
            style={{ marginTop: 20 }}
            className="comingFromRight"
            value={props.yearofjoining}
            onChange={props.handleYearChange}
          ></DatePicker>
        </MuiPickersUtilsProvider>
      </ThemeProvider>
    </div>
  );
};
