import React from "react";
import {
  TextField,
  createMuiTheme,
  ThemeProvider,
  InputAdornment,
  MenuItem,
  CircularProgress
} from "@material-ui/core";
import { green, orange } from "@material-ui/core/colors";
import { Phone, FormatListNumberedOutlined } from "@material-ui/icons";
const theme1 = createMuiTheme({
  palette: {
    primary: orange,
    secondary: green
  }
});

const bbaBranch = ["General", "Industry Integrated"];
const BTechBranch = [
  "Computer Science",
  "Mechanical",
  "Electrical",
  "Electronics",
  "Civil"
];

export default props => {
  return (
    <div>
      <ThemeProvider theme={theme1}>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <TextField
            label="Phone"
            type="phone"
            style={{ marginRight: 10 }}
            error={
              (props.phone.length !== 10 && props.phone !== "") ||
              props.phoneValid === false
            }
            fullWidth
            helperText={
              props.phone.length !== 10 && props.phone !== ""
                ? "Length Must Be 10"
                : props.phoneValid === false
                ? "Mobile Already Taken"
                : ""
            }
            FormHelperTextProps={{ className: "comingFromLeft" }}
            InputProps={{
              className: "comingFromUp",
              endAdornment: (
                <InputAdornment>
                  {props.isMobileLoading ? (
                    <CircularProgress
                      style={{ width: 20, height: 20 }}
                    ></CircularProgress>
                  ) : (
                    <Phone style={{ color: "grey" }}></Phone>
                  )}
                </InputAdornment>
              )
            }}
            value={props.phone}
            placeholder="+91"
            onChange={props.onChange}
            onBlur={props.onBlurPhone}
            name="phone"
          ></TextField>
          <TextField
            label="Roll No"
            fullWidth
            InputProps={{
              className: "comingFromUp",
              endAdornment: (
                <InputAdornment>
                  <FormatListNumberedOutlined></FormatListNumberedOutlined>
                </InputAdornment>
              )
            }}
            value={props.rollno}
            placeholder="Of Class"
            onChange={props.onChange}
            name="rollno"
          ></TextField>
        </div>

        <TextField
          label="Course"
          select
          style={{ marginTop: 20 }}
          fullWidth
          InputProps={{
            className: "comingFromUp"
          }}
          value={props.course}
          onChange={props.onChange}
          name="course"
        >
          <MenuItem key="b.tech" value="b.tech">
            Bachelor of technology
          </MenuItem>
          <MenuItem key="bba" value="bba">
            Bachelor of Business Administration
          </MenuItem>
        </TextField>
        <TextField
          label="Branch"
          select
          disabled={props.course !== "" ? false : true}
          fullWidth
          style={{ marginTop: 20 }}
          InputProps={{
            className: "comingFromUp"
          }}
          value={props.Branch}
          onChange={props.onChange}
          name="Branch"
        >
          {props.course == "b.tech"
            ? BTechBranch.map(Element => {
                return (
                  <MenuItem key={Element} value={Element}>
                    {Element}
                  </MenuItem>
                );
              })
            : ""}
          {props.course == "bba"
            ? bbaBranch.map(Element => {
                return (
                  <MenuItem key={Element} value={Element}>
                    {Element}
                  </MenuItem>
                );
              })
            : ""}
        </TextField>
      </ThemeProvider>
    </div>
  );
};
