import React from "react";
import {
  TextField,
  createMuiTheme,
  ThemeProvider,
  MenuItem,
  InputAdornment,
  CircularProgress
} from "@material-ui/core";
import { orange, green } from "@material-ui/core/colors";
import { Check } from "@material-ui/icons";
const inputFieldTheme = createMuiTheme({
  palette: {
    secondary: green,
    primary: orange
  }
});

export default props => {
  return (
    <ThemeProvider theme={inputFieldTheme}>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <TextField
          style={{ width: "100%", marginRight: 10 }}
          name="firstName"
          value={props.firstName}
          label="First Name"
          placeholder="Your First Name"
          className="comingFromLeft"
          InputProps={{
            style: {
              color: "rgba(0,0,0,0.8)",
              fontWeight: 400
            }
          }}
          margin="normal"
          onChange={props.handleChange}
        ></TextField>

        <TextField
          label="Last Name"
          fullWidth
          onChange={props.handleChange}
          name="LastName"
          value={props.LastName}
          placeholder="Your Last Name"
          className="comingFromRight"
          margin="normal"
          InputProps={{
            style: {
              color: "rgba(0,0,0,0.8)",
              fontWeight: 400
            }
          }}
        ></TextField>
      </div>

      <TextField
        label="Email"
        fullWidth
        onChange={props.handleChange}
        name="email"
        value={props.email}
        placeholder="Your Email Address"
        className="comingFromRight"
        margin="normal"
        onBlur={props.onBlur}
        error={!props.isEmailRight}
        FormHelperTextProps={{ className: "comingFromRight" }}
        helperText={!props.isEmailRight ? "Email Not Valid" : ""}
        InputProps={{
          style: {
            color: "rgba(0,0,0,0.8)",
            fontWeight: 400
          },
          endAdornment: (
            <InputAdornment>
              {props.isFetchingEmail ? (
                <CircularProgress
                  style={{ width: 20, height: 20 }}
                ></CircularProgress>
              ) : (
                ""
              )}
              {props.isEmailRight == true ? <Check></Check> : ""}
            </InputAdornment>
          )
        }}
      ></TextField>

      <TextField
        style={{ width: "100%", marginRight: 10 }}
        name="password"
        onChange={props.handleChange}
        value={props.password}
        label="New Password"
        type="password"
        placeholder="Greater Than 8 Character"
        className="comingFromLeft"
        error={props.password.length < 8 && props.password !== ""}
        helperText={
          props.password.length < 8 && props.password !== ""
            ? "Length Must Be Greater Than 8"
            : ""
        }
        FormHelperTextProps={{ className: "comingFromRight" }}
        InputProps={{
          style: {
            color: "rgba(0,0,0,0.8)",
            fontWeight: 400
          },
          className: "comingFromRight"
        }}
        margin="normal"
      ></TextField>

      <TextField
        label="Re-Enter Password"
        fullWidth
        onChange={props.handleChange}
        name="repass"
        type="password"
        value={props.repass}
        error={props.password !== props.repass ? true : false}
        placeholder="You Just Entered"
        className="comingFromRight"
        helperText={props.password !== props.repass ? "Password Not Match" : ""}
        FormHelperTextProps={{ className: "comingFromRight" }}
        margin="normal"
        InputProps={{
          style: {
            color: "rgba(0,0,0,0.8)",
            fontWeight: 400
          },
          className: "comingFromRight"
        }}
      ></TextField>
      <TextField
        select
        label="gender"
        className="comingFromUp"
        name="gender"
        onChange={props.handleChange}
        value={props.gender}
        InputProps={{ className: "comingFromRight" }}
        margin="normal"
        fullWidth
      >
        <MenuItem key={"male"} value="male">
          Male
        </MenuItem>
        <MenuItem key={"female"} value="female">
          Female
        </MenuItem>
      </TextField>
    </ThemeProvider>
  );
};
