import React from "react";

import { Image, Divider } from "semantic-ui-react";

export default (props) => {
  return (
    <div>
      <Image
        src={require("../../images/complainify.png")}
        size="tiny"
        centered
      ></Image>
      {props.h2 ? (
        <div>
          <h2
            style={{
              color: "grey",
              textAlign: "center",
              fontFamily: "poppins",
            }}
          >
            {props.heading}
          </h2>
          <h3
            style={{
              color: "orange",
              textAlign: "center",
              textTransform: "uppercase",
            }}
          >
            {props.username}
          </h3>
        </div>
      ) : (
        <div>
          <h1
            style={{
              color: "grey",
              textAlign: "center",
              fontFamily: "poppins",
              fontWeight: "bold",
            }}
          >
            {props.heading}
          </h1>
          <h2
            style={{
              color: "orange",
              textAlign: "center",
              textTransform: "uppercase",
            }}
          >
            {props.username}
          </h2>
        </div>
      )}

      {props.divider ? <Divider></Divider> : ""}
    </div>
  );
};
